<template>
  <jy-form-dialog ref="form" :form-options="options" :entity="entity" :rules="rules">
    <template slot="fieldset">
      <el-form-item class="jw-field jw-field-1" label="设备名称" prop="name">
        <el-input v-model="entity.name"/>
      </el-form-item>
      <el-form-item class="jw-field jw-field-1" label="厂家" prop="manufacturer">
        <el-input v-model="entity.manufacturer"/>
      </el-form-item>
      <el-form-item class="jw-field jw-field-1" label="价格(元)" prop="price">
        <el-input-number v-model="entity.price" :controls="false"/>
      </el-form-item>
      <el-form-item class="jw-field jw-field-1" label="入库日期" prop="purchasedDate">
        <el-date-picker v-model="entity.purchasedDate" type="date"/>
      </el-form-item>
      <el-form-item class="jw-field jw-field-2" label="备注" prop="remark">
        <el-input v-model="entity.remark" type="textarea" :autosize="{maxRows: 6}"/>
      </el-form-item>
    </template>
  </jy-form-dialog>
</template>


<script>
  import {DetailMixin} from 'mixins'

  export default {
    name: 'billentryDetail',
    mixins: [DetailMixin],
    data () {
      return {
        options: {
          size: 'small',
          context: {
            name: '出库单',
            url: '/api/BillentryAction.action',
            detailComponent: this
          },
          createEntity(options, cb) {
            cb({
              id: '',
              manufacturer: '',
              price: '',
              purchasedDate: this.$moment().toDate(),
              remark: ''
            })
          }
        },
        entity: {
        },
        rules: {
          name: [
            {required: true, message: '请输入设备名称', trigger: 'blur'},
            {max: 128, message: '长度在128字符以内', trigger: 'blur'}
          ],
        }
      }
    }
  }
</script>



